:root {
  --main-color: rgba(0, 0, 0, 0.65);
  --title-orange: rgba(252, 103, 68, 0.8);
  --title-black: rgba(0, 0, 0, 0.8);
  --card-text: rgba(255, 255, 255, 0.75);
  --first-card: #4eb2f7;
  --second-card: #c049f8;
  --third-card: #817eff;
  --fourth-card: #f74770;
  --white: rgba(255, 255, 255, 1);
  --black: rgba(0, 0, 0, 1);
  --header-bg: #f5f5f5;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  font-size: 100%;
  min-height: 100vh;
  font-family: 'Open Sans', sans-serif;
}
body {
  color: var(--main-color);
}
.container {
  max-width: 1140px;
  margin: auto;
  padding: 0 15px;
}
a {
  &:hover {
    text-decoration: none;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.loader {
  display: none;
  &.active {
    display: inline-block;
  }
}

#users {
  display: flex;
  flex-wrap: wrap;
  li {
      flex-basis: calc(50% - 5px);
  }
  li:not(:last-child) {
    margin-bottom: 12px;
  }
  li:nth-child(odd) {
      margin-right: 10px;
  }
}

.modal {
  text-align: center;
}
